





















import { Component, Mixins, Watch, Prop } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";

import { DatePickerOptions } from "element-ui/types/date-picker";
@Component({
    name: "MyDateRangePicker"
})
export default class MyDateRangePicker extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;

    @Prop({
        default: true,
        type: Boolean
    })
    clearable!: boolean;

    @Prop({
        default: false,
        type: Boolean
    })
    formerOnly!: boolean;

    @Prop({
        default: false,
        type: Boolean
    })
    afterToday!: boolean;

    @Prop({
        default: false,
        type: Boolean
    })
    formerTodayOnly!: boolean;

    getEndDate() {
        const end = new Date();
        if (this.formerTodayOnly) {
            end.setTime(end.getTime() - 24 * 60 * 60 * 1000);
        }
        return end;
    }
    get shortcutsList() {
        const $this = this;
        const shortcuts = [
            {
                text: "昨天",
                onClick(picker: any) {
                    const start = new Date(
                        new Date(new Date().getTime() - 24 * 60 * 60 * 1000).setHours(0, 0, 0, 0)
                    );
                    const end = new Date(
                        new Date(new Date().getTime() - 24 * 60 * 60 * 1000).setHours(
                            23,
                            59,
                            59,
                            59
                        )
                    );
                    picker.$emit("pick", [start, end]);
                }
            },
            {
                text: "本周",
                onClick(picker: any) {
                    const end = $this.getEndDate();
                    let day = end.getDay();

                    if (!day) day = 6;
                    else day -= 1;

                    const start = new Date();
                    start.setTime(end.getTime() - 3600 * 1000 * 24 * day);

                    picker.$emit("pick", [start, end]);
                }
            },
            {
                text: "本月",
                onClick(picker: any) {
                    const end = $this.getEndDate();
                    const date = end.getDate();
                    const start = new Date();
                    start.setTime(end.getTime() - 3600 * 1000 * 24 * (date - 1));
                    picker.$emit("pick", [start, end]);
                }
            },
            {
                text: "上周",
                onClick(picker: any) {
                    const now = new Date();
                    const dayNow = now.getDay();

                    const start = new Date();
                    const end = $this.getEndDate();

                    start.setTime(now.getTime() - 3600 * 1000 * 24 * (dayNow + 7));
                    end.setTime(now.getTime() - 3600 * 1000 * 24 * (dayNow + 1));

                    picker.$emit("pick", [start, end]);
                }
            },
            {
                text: "上个月",
                onClick(picker: any) {
                    const now = new Date();
                    const monthNow = now.getMonth();
                    const end = $this.getEndDate();

                    end.setMonth(monthNow, 0);
                    const lastMonthDays = end.getDate();

                    const start = new Date();
                    start.setTime(end.getTime() - 3600 * 1000 * 24 * (lastMonthDays - 1));

                    picker.$emit("pick", [start, end]);
                }
            },
            {
                text: "最近一周",
                onClick(picker: any) {
                    const end = $this.getEndDate();
                    const start = new Date();
                    start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                    picker.$emit("pick", [start, end]);
                }
            },
            {
                text: "最近三十天",
                onClick(picker: any) {
                    const start = new Date();
                    const end = $this.getEndDate();
                    start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                    picker.$emit("pick", [start, end]);
                }
            }
        ];
        if (!this.formerTodayOnly) {
            shortcuts.splice(0, 0, {
                text: "今天",
                onClick(picker: any) {
                    const now = new Date();
                    picker.$emit("pick", [now, now]);
                }
            });
        }
        return shortcuts;
    }

    get formatePickerOptions() {
        const { formerOnly, formerTodayOnly, afterToday } = this;

        const result: DatePickerOptions = {
            shortcuts: this.shortcutsList
        };

        if (formerOnly) {
            result.disabledDate = time => {
                return time.getTime() > Date.now();
            };
        } else if (formerTodayOnly) {
            result.disabledDate = time => {
                return time.getTime() > Date.now() - 86400000;
            };
        } else if (afterToday) {
            result.disabledDate = time => {
                return time.getTime() < Date.now() - 86400000;
            };
        }
        return result;
    }

    @Watch("currentValue")
    watchCurrentValue(newVal: any) {
        if (newVal === null) {
            this.currentValue = [];
        }
        if (newVal && newVal.length) {
            const [start, end] = this.currentValue;
            this.currentValue[0] = `${start} 00:00:00`;
            this.currentValue[1] = `${end} 23:59:59`;
        }
    }
}
